import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { TokenStorageService } from './token-storage.service';

@Injectable({
  providedIn: 'root',
})
export class SessionService {
  protected basePath = environment.microServiceURL;

  constructor(
    private httpClient: HttpClient,
    private tokenService: TokenStorageService
  ) {}

  signin(data: any, isRememberMe: boolean, otpRequired: boolean) {
    return this.httpClient
      .post<any>(
        `${this.basePath}/auth/signin?rememberMe=${isRememberMe}&otpRequired=${otpRequired}`,
        data
      )
      .pipe(
        map((res) => {
          if (res?.accessToken) {
            return this.tokenService.saveToken(res?.accessToken);
          }
        })
      );
  }

  getProfileInfo() {
    return this.httpClient.get<any>(`${this.basePath}/loginApi/profile`);
  }

  generateOTP() {
    return this.httpClient.get<any>(`${this.basePath}/auth/generateOTP`);
  }

  verifyOTP(payload: any) {
    return this.httpClient
      .post<any>(`${this.basePath}/auth/verifyOTP`, payload)
      .pipe(
        map((res) => {
          if (res?.accessToken) {
            return this.tokenService.saveToken(res?.accessToken);
          }
        })
      );
  }

  fetchCustomersByMobile(mobile: any) {
    return this.httpClient.get<any>(
      `${this.basePath}/customer?phoneNumber=${mobile}`
    );
  }

  getCustomerByNumber(customerNo: any) {
    return this.httpClient.get<any>(
      `${this.basePath}/customer/accountInfo?customerNo=${customerNo}`
    );
  }
}
