import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  OnInit,
  ViewChild,
} from '@angular/core';

import { ModalController, ToastController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { Subject } from 'rxjs';
import { DataService } from 'src/app/services/data.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, NavigationExtras } from '@angular/router';
import { LocalStoreService } from 'src/app/services/local-store.service';
import { SessionService } from 'src/app/services/session.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';
// import { Roles } from 'src/app/services/user-roles';
import {
  MOBILE_AGENT_ROLE_USER,
  MOBILE_CUSTOMER_ROLE_USER,
  Roles,
  TEST_BEARER_TOKEN,
  TEST_USER_MOBILE,
  staticData,
} from 'src/app/config/db.constant';
import { RelativeUsersComponent } from 'src/app/components/relative-users/relative-users.component';

const REMEMBER_ME = 'isRemember';

export class otpModel {
  source_key: any;
  source_value: any;
  source: any;
  isMobileLogin: boolean;
}

export class verifyotpModel {
  sourceKey: any;
  sourceValue: any;
  otp: number;
  type: any;
}

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  image: string;
  loginForm: FormGroup;
  otpValue: any = null;
  otpValid: boolean = false;
  oTpModel = new otpModel();
  verifyOtpModel = new verifyotpModel();
  otpResponse: any;
  customerPhonenum: any;
  userResp: boolean = false;
  showPassword: boolean = false;
  userPasswordUpdate = new Subject<string>();
  isLoading: boolean = false;
  loginDetails: any;

  constructor(
    private router: Router,
    private cdk: ChangeDetectorRef,
    private fb: FormBuilder,
    private api: ApiService,
    public toastCtrl: ToastController,
    private dataService: DataService,
    private ls: LocalStoreService,
    private sessionService: SessionService,
    private tokenService: TokenStorageService,
    private modalCtrl: ModalController
  ) {}

  ngOnInit() {
    this.initForm();

    let hasRemberObj: any;
    hasRemberObj = this.ls.getItem(REMEMBER_ME) || {};

    if (hasRemberObj?.rememberMe) {
      delete hasRemberObj['otpRequired'];
      this.loginForm.patchValue(hasRemberObj);
    }
  }

  initForm() {
    this.loginForm = this.fb.group({
      phoneNo: [''],
      password: ['', Validators.required],
      rememberMe: [false],
      otpRequired: [false],
      // old
      isOtp: [false],
      otp: [''],
    });
  }

  get isOtpRequired(): any {
    return this.loginForm.get('otpRequired');
  }

  public onPasswordToggle(showType): void {
    if (showType === 'new') {
      this.showPassword = !this.showPassword;
    }
  }

  _keyPress(event: any) {
    console.log('key', event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  /**
   * To submit the signin form
   */
  onSubmit() {
    if (this.loginForm.invalid) {
      return;
    }

    this.isLoading = true;

    /* send username and password to get Access Token */
    let payload = {
      mobile: this.loginForm.value['phoneNo'],
      password: this.loginForm.value['password'],
    };

    let isRememberMe = this.loginForm.value['rememberMe'];
    let otpRequired = this.loginForm.value['otpRequired'];
    this.ls.setItem(REMEMBER_ME, this.loginForm.value);

    this.sessionService.signin(payload, isRememberMe, otpRequired).subscribe(
      (_) => {
        this.isLoading = false;

        /* get profile info */
        if (this.loginForm.value['otpRequired']) {
          // this.show2Factor = true;
        } else {
          this.getProfile();
        }
      },
      (err) => {
        this.isLoading = false;
        // this.simulateLogin();
      }
    );
  }

  /**
   * @method getProfile()
   */
  getProfile() {
    this.sessionService.getProfileInfo().subscribe(
      (res) => {
        // this.spinner?.hide();
        let role: any = res.roles[0];

        if (role?.name === 'MOBILE_CUSTOMER') {
          this.tokenService.saveUser(res);
          this.tokenService.setLoggedIn(false);
          this.dataService.isLoggedIn.next(true);
          this.getCustomerListByMobile(res?.mobile);
        } else if (role?.name === 'MOBILE_AGENT') {
          this.tokenService.saveUser(res);
          this.dataService.isLoggedIn.next(true);
          this.tokenService.setLoggedIn(true);
          this.router.navigate(['dashboard'], { replaceUrl: true });
          this.openToast(`Welcome, ${res?.username}!`);
        } else {
          this.router.navigate(['no-permission'], { replaceUrl: true });
        }
      },
      (err) => {
        // this.spinner?.hide();
        // this.simulateLogin();
      }
    );
  }

  getCustomerListByMobile(mobile) {
    this.sessionService.fetchCustomersByMobile(mobile).subscribe(
      (res) => {
        if (res?.statusCode == 200) {
          localStorage.setItem('customer_details', JSON.stringify(res?.data));
          this.openAvailableUserModal(mobile, res?.data);
        } else {
          this.openToast(`${res?.message}`);
        }
      },
      (err) => {
        console.log(err);
      }
    );
  }

  getCustomerByNumber(customerNo) {
    this.sessionService.getCustomerByNumber(customerNo).subscribe(
      (res) => {
        if (res?.statusCode == 200) {
          localStorage.setItem('ACCOUNT', JSON.stringify(res?.data));
          this.tokenService.setLoggedIn(true);
          this.router.navigate(['dashboard'], { replaceUrl: true });
        } else {
          this.openToast(`${res?.message}`);
        }
      },
      (err) => {
        console.log(err);
      }
    );
  }

  // Opening relative user sheet
  async openAvailableUserModal(mobile, data: any) {
    const modal = await this.modalCtrl.create({
      component: RelativeUsersComponent,
      breakpoints: [0, 0.75],
      initialBreakpoint: 0.75,
      handle: false,
      cssClass: 'relative-modal',
      componentProps: {
        users: data,
        mobile: mobile,
      },
      backdropDismiss: false,
      canDismiss: true,
    });
    await modal.present();

    modal.onDidDismiss().then((res) => {
      console.log(res);
      if (res?.data) {
        this.getCustomerByNumber(res?.data?.customerId);
      }
    });
  }

  continue(loginForm) {
    if (!this.loginForm.get('isOtp').value) {
      if (!loginForm.phoneNo) {
        this.openToast('Phone number is required!');
        return;
      }
      if (!loginForm.password) {
        this.openToast('Password is required!');
        return;
      }
      this.isLoading = true;

      let payload = {
        phoneNumber: loginForm.phoneNo,
        password: loginForm.password,
      };

      this.api.validatePassword(payload).subscribe(
        (res: any) => {
          if (res.status == 200) {
            if (res['data']?.firstTimeLogin == 'Y') {
              localStorage.setItem('customerPhonenum', loginForm.phoneNo);
              const navigationExtras: NavigationExtras = {
                queryParams: {
                  screenName: 'reset',
                },
              };
              this.api.sendNavParam(navigationExtras);
              this.router.navigateByUrl('/new-passwordchange');
            } else {
              localStorage.setItem('customerPhonenum', loginForm.phoneNo);
              sessionStorage.setItem('customer_id', res['data']?.customerId);
              localStorage.setItem('firstName', res['data']?.firstName);
              localStorage.setItem('lastName', res['data']?.lastName);
              localStorage.setItem('userType', res['data']?.userType);
              localStorage.setItem('customer_id', res['data']?.customerId);
              localStorage.setItem(
                'customer_details',
                JSON.stringify(res['data'])
              );
              localStorage.setItem('isShowed', 'no');
              this.router.navigate(['dashboard'], { replaceUrl: true });
              this.dataService.isLoggedIn.next(true);
              this.openToast('Logged in Successfully!');
            }
          } else {
            this.openToast(res?.message);
          }
          this.isLoading = false;
        },
        (err: any) => {
          this.isLoading = false;
        }
      );
    } else {
      this.getOtp(loginForm);
    }
  }

  getOtp(phone: any) {
    this.customerPhonenum = phone.phoneNo;
    this.oTpModel.source = 'customer';
    this.oTpModel.source_key = 'mobile';
    this.oTpModel.source_value = phone.phoneNo;
    this.oTpModel.isMobileLogin = true;

    if (this.customerPhonenum == '') {
      this.openToast('Please enter the registered Mobile Number');
      return;
    }

    this.api.getOtp(this.oTpModel).subscribe(
      (res: any) => {
        if (res.status == 200) {
          localStorage.setItem('customerPhonenum', this.customerPhonenum);
          this.router.navigate(['otp']);
          this.openToast(res?.message);
          const navigationExtras: NavigationExtras = {
            queryParams: {
              screenName: 'login',
            },
          };
          this.api.sendNavParam(navigationExtras);
          this.loginForm.reset();
        } else {
          this.openToast(res?.message);
        }
      },
      (err: any) => {
        this.openToast(err);
      }
    );
  }

  async openToast(message) {
    const toast = await this.toastCtrl.create({
      message: `${message}`,
      duration: 2500,
      position: 'bottom',
    });
    toast.present();
  }

  back() {
    this.router.navigateByUrl('login-landing');
  }

  forgotPassword() {
    if (this.loginForm.get('phoneNo')?.valid) {
      this.router.navigateByUrl('/forget-password', {
        state: { data: this.loginForm.value.phoneNo },
      });
    } else {
      this.router.navigateByUrl('/forget-password');
    }
  }

  ionViewWillLeave() {
    this.loginForm.get('isOtp')?.patchValue(false);
  }
}
